.container {
	margin: 0 auto;
	max-width: 1920px;
	padding: 0 5%;
	@include r(767) {
		padding: 0; }
	@include rmin(1400) {
		padding: 0 8%; }
	&--h {
		height: 100%; }
	&-inner {
		padding: 0 13.65%;
		@include rmin(1400) {
			padding: 0 16.25%; }
		@include r(767) {
			padding: 0 40px; } } }

.modal-container {
	max-width: 1920px;
	margin: 0 5%;
	@include r(767) {
		margin: 0; } }

.row {
	display: flex;
	margin: 0 -15px;
	flex-wrap: wrap; }

.col-1-3 {
	flex: 0 0 $grid_1_3;
	flex-basis: $grid_1_3;
	padding: 0 15px;
	max-width: $grid_1_3;
	&_md {
		@include rmin(992) {
			flex: 0 0 $grid_1_3;
			flex-basis: $grid_1_3;
			padding: 0 15px;
			max-width: $grid_1_3; } }
	&_lg {
		@include rmin(1200) {
			flex: 0 0 $grid_1_3;
			flex-basis: $grid_1_3;
			padding: 0 15px;
			max-width: $grid_1_3; } } }

