.sc {
  padding: $section_pad 0;
  &__top-links {
    margin-bottom: 40px; }
  &__heading {
    position: relative;
    padding: $heading_pad 0;
    line-height: 1;
    &:before {
      content: '';
      position: absolute;
      width: 3px;
      height: 0.84em;
      background: $color_secondary;
      display: inline-block;
      vertical-align: middle;
      left: -44px;
      top: 50%;
      transform: translateY(-50%);
      margin-top: .1em;
      @include r(767) {
        left: -24px; } }
    &--bwhite:before {
      background: #ffffff; }
    &--pb20 {
      padding-bottom: 20px;
      &:before {
        margin-top: 20px; } } }
  &.bg-gradient {
    position: relative;
    background: $color_gray;
    $height_block: 450px;
    background: -moz-linear-gradient(top, $color_white $height_block, $color_gray $height_block, $color_gray 100%);
    background: -webkit-linear-gradient(top, $color_white $height_block,$color_gray $height_block,$color_gray 100%);
    background: linear-gradient(to bottom, $color_white $height_block,$color_gray $height_block,$color_gray 100%);
    &--h300 {
      $height_block: 300px;
      background: -moz-linear-gradient(top, $color_white $height_block, $color_gray $height_block, $color_gray 100%);
      background: -webkit-linear-gradient(top, $color_white $height_block,$color_gray $height_block,$color_gray 100%);
      background: linear-gradient(to bottom, $color_white $height_block,$color_gray $height_block,$color_gray 100%); }
    &--h625 {
      $height_block: 625px;
      background: -moz-linear-gradient(top, $color_white $height_block, $color_gray $height_block, $color_gray 100%);
      background: -webkit-linear-gradient(top, $color_white $height_block,$color_gray $height_block,$color_gray 100%);
      background: linear-gradient(to bottom, $color_white $height_block,$color_gray $height_block,$color_gray 100%); }
    &--h380 {
      $height_block: 380px;
      background: -moz-linear-gradient(top, $color_white $height_block, $color_gray $height_block, $color_gray 100%);
      background: -webkit-linear-gradient(top, $color_white $height_block,$color_gray $height_block,$color_gray 100%);
      background: linear-gradient(to bottom, $color_white $height_block,$color_gray $height_block,$color_gray 100%); } } }
