.nav .nav__link {
  display: inline-block;
  position: relative;
  overflow: hidden;
  padding: 2px 20px;
  line-height: 1;
}

@media only screen and (max-width: 1700px) {
  .nav .nav__link {
    padding: 1px 15px;
  }
}

@media only screen and (max-width: 1199px) {
  .nav .nav__link {
    padding: 1px 8px;
  }
}

.nav .nav__link:after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  z-index: 0;
  background: #05c3de;
  transform: translate3d(102%, 0, 0);
  transition: transform 0.5s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

.nav .nav__link:hover::after {
  transform: translate3d(0, 0, 0);
}

.nav .nav__link:hover .nav__t:before {
  width: 100%;
}

.nav .nav__t {
  display: block;
  position: relative;
  z-index: 1;
}

.nav .nav__t:before {
  content: attr(data-letters);
  position: absolute;
  color: #ffffff;
  left: 0;
  overflow: hidden;
  white-space: nowrap;
  width: 0%;
  transition: width 0.5s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

