  .founder__b-2-hide {
    display: none;
  }
}

@media only screen and (max-width: 1500px) {
  .founder__b-2 {
    display: none;
  }
}

@media only screen and (max-width: 1199px) {
  .founder__b-2-hide {
    display: inline;
  }
}

.news {
  position: relative;
  z-index: 1;
}

.frontpage .news {
  background: #f7f7f7;
  margin-top: -60px;
}

.news .post__text, .news .post__title {
  transform: translateX(-15px);
  opacity: 0;
  transition: transform 600ms cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 600ms cubic-bezier(0.215, 0.61, 0.355, 1), opacity 650ms cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-delay: .2s;
}

.news__blocks {
  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
}

.news__col {
  flex: 0 0 50%;
}

@media only screen and (max-width: 991px) {
  .news__col {
    flex: 0 0 100%;
  }
}

.news__item {
  position: relative;
}

.no-touch .news__item:hover .news__content {
  opacity: 1;
}

.no-touch .news__item:hover .post__text, .no-touch .news__item:hover .post__title {
  opacity: 1;
  transform: translateX(0);
}

.news__content {
  position: relative;
  padding: 35px;
  background: #05c3de;
  opacity: 0;
  overflow: hidden;
  transition: opacity .3s;
  z-index: 1;
}

.news__face {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.news__background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-position: 50% 50%;
  background-repeat: none;
  background-size: cover;
  z-index: 0;
}
