.inline-list {
  font-family: $font_secondary;
  font-weight: 400;
  &__item {
    display: inline-block;
    &:after {
      content: '';
      position: relative;
      width: 3px;
      height: 0.84em;
      background: $color_secondary;
      display: inline-block;
      vertical-align: middle;
      margin:  0px 25px 0px; }
    &:last-child:after {
      display: none; } }
  &__link {
    display: inline-block;
    vertical-align: middle;
    color: $color_t;
    text-transform: uppercase;
    text-decoration: none;
    transition: color .3s;
    @include hover {
      color: $color_secondary; } } }
